import React, { useState } from 'react';
import '../dist/css/Contact.css';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Loader from '../components/Loader';

export default function Contact() {
    document.title = "RNR | Contact"
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState({
        name: '',
        phone_no: '',
        message: ''
    });
    const handleChange = (e) => {
        setMessage({
            ...message,
            [e.target.id]:e.target.value
        })
    }
    const handleSubmit = async(e) => {
        e.preventDefault();
        setLoading(true);

        const response = await fetch(`/api/message`, {
            method: 'POST',
            body: JSON.stringify(message),
            headers: {'Content-Type': 'application/json'}
        })
        setLoading(false);
        const json = await response.json();
        if (json.success) {
            setTimeout(() => {
                alert('We will contact you shortly');
            },200)
        }
        else {
            setTimeout(() => {
                alert('Failed to send message');
            }, 200)
        }

    }
    return (
        <div>
            <Navbar />
            {loading && <Loader />}
            <section className="form-dark">

                <div className="card card-image">
                    <form className="text-white rgba-stylish-strong z-depth-2" onSubmit={handleSubmit}>

                        <div className="text-center">
                            <h3 className="white-text mb-5 mt-4 font-weight-bold"><strong>CONTACT</strong> <a className="green-text font-weight-bold"><strong> US</strong></a></h3>
                        </div>

                        <div className="md-form">
                            <input type="text" id="name" className="form-control white-text" value={message.name} onChange={handleChange} required/>
                            <label htmlFor="name">Your name</label>
                        </div>

                        <div className="md-form">
                            <input type="text" id="phone_no" className="form-control" value={message.phone_no} onChange={handleChange} required/>
                            <label htmlFor="phone_no" className="">Your Phone Number</label>
                        </div>

                        <div className="md-form">
                            <textarea type="text"
                                id="message"
                                name="message5"
                                rows="4"
                                className="form-control md-textarea white-text"
                                onChange={handleChange}
                                required
                            >
                                {message.message}
                            </textarea>
                            <label htmlFor="message">Your message</label>
                        </div>
                        

                        <div className="row d-flex align-items-center">

                            <div className="text-center mb-3 col-md-12">
                                <button type="submit" className="btn btn-success btn-block btn-rounded z-depth-1 waves-effect waves-light">Send message</button>
                            </div>

                        </div>


                    </form>
                </div>

            </section>
            <Footer/>
        </div>
    );
};

