import React, { useEffect, useState } from 'react';
import L from 'leaflet';

const modalWrapper = {
    position: 'fixed',
    left: 0,
    top: 0,
    bottom: 0,
    right: 0,
    backgroundColor: 'rgba(189, 189, 189, 0.9)'
}

const modalContainer = {
    position: 'fixed',
    minWidth: '50vw',
    maxWidth: '95vw',
    width: '25rem',
    top: '50vh',
    left: '50vw',
    transform: 'translate(-50%,-50%)',
    backgroundColor: '#fff',
    padding: '10px',
    borderRadius: '0.5rem'
}



const LocationModal = ({ closeModal, setAddress, address, onChange }) => {
    const [coords, setCoords] = useState({latitude: 0, longitude: 0});

    const getCurrentLocation = () => {
        navigator.geolocation.getCurrentPosition(
            (pos) => {
                setCoords(pos.coords);
            },
            (err) => {
            },
            {
                enableHighAccuracy: true,
                timeout: 5000,
                maximumAge: 0,
            }
        );
    };
    useEffect(() => { 
        getCurrentLocation();
    },[])
    useEffect(() => {
            // Initialize the Leaflet map
            const map = L.map('map').setView([coords.latitude, coords.longitude], 13);

            // Add the OpenStreetMap tiles to the map
            L.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
                maxZoom: 19,
                attribution: ' OpenStreetMap'
            }).addTo(map);

            // Add a marker to the map
            const marker = L.marker([coords.latitude, coords.longitude]).addTo(map);


            map.on('click', (e) => {
                const newCoords = {
                    latitude: e.latlng.lat,
                    longitude: e.latlng.lng
                };
                setCoords(newCoords);
                marker.setLatLng([newCoords.latitude, newCoords.longitude]);
            });

        const fetchAddress = async () => {
            // Fetch the address using reverse geocoding
            try {
                const response = await fetch(
                    `https://nominatim.openstreetmap.org/reverse?format=json&lat=${coords.latitude}&lon=${coords.longitude}`
                );
                const data = await response.json();
                setAddress({ coords: coords, data: data.address });
                onChange(address);
            } catch (error) {
                console.error('Error fetching address:', error);
                setAddress(null);
            }
        }
        fetchAddress(); 
        return () => {
            map.remove();
        };
    }, [coords]);
    const setCurrentLocation = async() => {
        getCurrentLocation();

    }
    return(
        <div>
            <div className="modal-wrapper" style={modalWrapper} onClick={closeModal}></div>
            <div className="modal-body" style={modalContainer}>
                <div className="d-flex justify-content-between">
                    <h5 className="modal-title">Choose Location</h5>
                    <button type="button" className="btn btn-lg" onClick={closeModal}>
                        <span>&times;</span>
                    </button>
                </div>
                <div id="map" style={{ width: '100%', height: '20rem' }}></div>
                <div>
                    <button type='button' className='btn btn-outline-danger m-2' onClick={setCurrentLocation}>use my current location</button>
                
                    <button type='button' className='btn btn-outline-success m-2' onClick={() => closeModal(address)}>save</button>
                </div>
                <div>
                    {
                        address && address.data ?
                            Object.keys(address.data).map((key) => <div>{`${key} : ${address.data[key]}`}</div>)
                            : ''
                    }
                </div>
            </div>
        </div>
    )
}


const AddressForm = (props) => {
    const [showModal, setShowModal] = useState(false);
    const [address, setAddress] = useState({
        coords: null,
        data: null
    })
    
    const handleChoose = () => {
        setShowModal(true);
    }

    return (
        <div className={props.className}>
            <div className="row" onClick={handleChoose} style={{ cursor: 'pointer' }}>
                <div className="col">
                    <div>
                        {
                            address && address.data ?
                                Object.keys(address.data).map((key) => <div>{`${key} : ${address.data[key]}`}</div>)
                                : 'choose address'
                        }
                    </div>
                </div>
            </div>
            {
                showModal &&
                <LocationModal
                    closeModal={
                        (address) => {
                            props.onChange(address);
                            setShowModal(false);
                        }
                    }
                    setAddress={setAddress}
                    address={address}
                    onChange={props.onChange}
                />
            }
        </div>
    );
};

export default AddressForm;
