import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import { Link, useNavigate } from "react-router-dom";
import { useUserContext } from "../hooks/useUserContext";
import Loader from "../components/Loader";
import Footer from "../components/Footer";


export default function Register() {
    document.title = "RNR | Register";
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
    const { state,dispatch } = useUserContext();
    const { user, reminders } = state;

    const [reminder, setReminder] = useState({
        user: null,
        reg_no: '',
        owner_name: '',
        phone_no: '',
        alt_no: '',
        whatsapp_no: '',
        expiry_date: '',
        pincode: ''
    });

    useEffect(() => {

        if (!user) {
            navigate('/');
        }
        else {
            setReminder({...reminder, user})
        }
    }, [navigate, user]);

    

    const handleChange = (e) => {
        setReminder({
            ...reminder,
            [e.target.id]: e.target.value,
        });
    };
    const handleChangeNumber = (e) => { 
        if (e.target.checked) 
            setReminder({...reminder, whatsapp_no: reminder.phone_no})
        
        else
            setReminder({ ...reminder, whatsapp_no: '' })
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        setLoading(true);
        const response = await fetch('/api/addreminder', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(reminder)
        });
        setLoading(false);

        const json = await response.json();
        if (!json.success) {
            setTimeout(() => {
                alert(json.errors);
            }, 200);
        }
        else {
            dispatch({ type: 'ADD_REMINDER', payload: { reminder: json.reminder }});
            alert('Registration Successful');
            navigate('/profile');
        }
        
    };

    
    return (
        <div>
            <Navbar />
            {loading && <Loader />}
            <section className="pb-3" style={{ backgroundColor: "#eee" }}>
                <div className="container">
                    <div className="row d-flex justify-content-center">
                        <div className="col-lg-12 col-xl-11 mt-3">
                            <div className="card text-black" style={{ borderRadius: "5px" }}>
                                <div className="card-body p-md-5">
                                    <div className="row justify-content-center">
                                        <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">
                                            <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">
                                                Register For Free PUCC Reminder
                                            </p>

                                            <form className="mx-1 mx-md-4" onSubmit={handleSubmit}>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="reg_no"
                                                            className="form-control border"
                                                            value={reminder.reg_no.toUpperCase()}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Registration Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="owner_name"
                                                            className="form-control border"
                                                            value={reminder.owner_name}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Owner Name
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="phone_no"
                                                            className="form-control border"
                                                            value={reminder.phone_no}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Phone Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="alt_no"
                                                            className="form-control border"
                                                            value={reminder.alt_no}
                                                            onChange={handleChange}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Alternative Phone Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="whatsapp_no"
                                                            className="form-control border"
                                                            value={reminder.whatsapp_no}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Whatsapp No
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="form-check d-flex justify-content-center mb-5">
                                                    <input
                                                        className="form-check-input me-2"
                                                        type="checkbox"
                                                        id="same_number"
                                                        onClick={handleChangeNumber}
                                                    />
                                                    <label className="form-check-label" htmlFor="same_number">
                                                        Same as phone number
                                                    </label>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="pincode"
                                                            className="form-control border"
                                                            value={reminder.pincode}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Pincode
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="date"
                                                            id="expiry_date"
                                                            className="form-control border"
                                                            value={reminder.expiry_date}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Expiry Date
                                                        </label>
                                                    </div>
                                                </div>

                                                <div className="form-check d-flex justify-content-center mb-5">
                                                    <input
                                                        className="form-check-input me-2"
                                                        type="checkbox"
                                                        id="terms"
                                                        onClick={(e) =>
                                                        (document.querySelector("#registerBtn").disabled =
                                                            !e.target.checked)
                                                        }
                                                    />
                                                    <label className="form-check-label" htmlFor="terms">
                                                        I agree all statements in 
                                                        <a href="#!">Terms of service</a>
                                                    </label>
                                                </div>

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button
                                                        id="registerBtn"
                                                        type="submit"
                                                        className="btn btn-primary btn-lg"
                                                        disabled
                                                    >
                                                        Register
                                                    </button>
                                                </div>

                                            </form>
                                        </div>
                                        <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2 h-100">
                                            <img
                                                src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp"
                                                className="img-fluid"
                                                alt="Sample image"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </div>
    );
}
