import React, { useEffect, useState } from 'react';
import Navbar from '../components/Navbar';
import Reminder from '../components/Reminder';
import { useNavigate } from 'react-router-dom';
import { useUserContext } from '../hooks/useUserContext';
import { getUserFromToken } from '../utils/Auth';
import BuyRequest from '../components/BuyRequest';
import Footer from '../components/Footer';
import SellRequest from '../components/SellRequest';

export default function Profile() {
    const elements = document.getElementsByClassName('user-details');
    const btns = document.getElementsByClassName('update-btns');
    const [userDetails, setUserDetails] = useState({
        name: '',
        email: '',
        phone_no: ''
    });
    const { state , dispatch} = useUserContext();
    const { user, reminders, sellrequests, buyrequests } = state;

    const navigate = useNavigate();
    document.title = 'RNR | My Profile';


    const token = localStorage.getItem('authToken'); 


    const fetchReminders = async (userId) => {
        try {
            const response = await fetch(`/api/reminders?user=${userId}`);
            const json = await response.json();
            if (json.success) {
                dispatch({ type: 'SET_REMINDERS', payload: { reminders: json.reminders } });
            } else {
            }
        } catch (error) {
            console.error('Error fetching reminders:', error);
        }

    };
    const fetchBuyRequests = async (userId) => {
        try {
            const response = await fetch(`/api/buyrequest/${userId}`);
            const json = await response.json();
            if (json.success) {
                dispatch({ type: 'SET_BUY_REQUESTS', payload: { buyrequests: json.buyrequests } });
            } 
        } catch (error) {
            console.error('Error fetching buyrequests:', error);
        }

    };
    const fetchSellRequests = async (userId) => {
        try {
            const response = await fetch(`/api/sellrequests/?user=${userId}`);
            const json = await response.json();
            if (json.success) {
                dispatch({ type: 'SET_SELL_REQUESTS', payload: { sellrequests: json.sellrequests } });
            }
        } catch (error) {
            console.error('Error fetching sellrequests:', error);
        }

    };
    const fetchUser = async (token) => {
        const user = await getUserFromToken(token);
        return user;
    }
    

    useEffect(() => {
        if(user)
            setUserDetails({ ...user });
        else {
            const fetchUserData = async () => {
                if (!token) {
                    dispatch({ type: 'SET_USER', payload: { user: null } });
                    navigate('/login');
                } else {
                    try {
                        const userData = await fetchUser(token);
                        dispatch({ type: 'SET_USER', payload: { user: userData } });
                        await fetchReminders(userData._id);
                        await fetchBuyRequests(userData._id);
                        await fetchSellRequests(userData._id);
                    } catch (error) {
                        dispatch({ type: 'SET_USER', payload: { user: null } });
                        navigate('/login');
                    }
                }
            };

            fetchUserData();
        }

        
    }, [user]);

    if (!user) {
        return null;
    }

    const changeUserDetails = (e) => {
        setUserDetails({
            ...userDetails,
            [e.target.id]:e.target.value
        })

    }

    const handleEdit = () => {
        for (let ele of elements) 
            ele.removeAttribute("readOnly");
        for (let btn of btns)
            btn.removeAttribute('hidden');

    }

    const saveUpdate = async () => {
        for (let ele of elements) 
            ele.setAttribute("readOnly", true);
        for (let btn of btns)
            btn.setAttribute('hidden', true);
        

        const response = await fetch('/api/updateuser', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(userDetails)
        });
        const json = await response.json();
        if (json.success) {
            dispatch({
                type: 'SET_USER', payload: {user: userDetails}
            });

        }
        else {
            setUserDetails({ ...user });
        }
    }

    const cancelUpdate = () => {
        for (let ele of elements) 
            ele.setAttribute("readOnly", true);
        for (let btn of btns)
            btn.setAttribute('hidden', true);

        setUserDetails({ ...user });
    }

    return (
        <div>
            <Navbar />
            
            <section className="h-100 gradient-custom-2">
                <div className="container py-5 h-100">
                    <div className="row d-flex justify-content-center align-items-center h-100">
                        <div className="col col-lg-9 col-xl-7">
                            <div className="card">
                                <div className="rounded-top text-white d-flex flex-row" style={{ backgroundColor: "#000", height:"200px"}}>
                                    
                                    <div className="ms-3" style={{ marginTop: 130 }}>
                                        <h5>Welcome {user.name}</h5>
                                        <p></p>
                                    </div>
                                </div>
                                <div className="p-4 text-black" style={{ backgroundColor: "#f8f9fa" }}>
                                    <div className="d-flex justify-content-between align-items-end text-center py-1">
                                        
                                        <button type="button" className="btn btn-sm btn-outline-dark"
                                                onClick={handleEdit}
                                                style={{ zIndex: "1" }}>
                                                Edit profile
                                        </button>
                                        <div className="d-flex flex-column" style={{gap: "5px"}}>
                                            <button type="button" className="btn btn-sm btn-outline-success m-2" onClick={() => navigate('/register')}>
                                                register for PUCC reminder
                                            </button>
                                            
                                            <button type="button" className="btn btn-sm btn-outline-danger m-2" onClick={() => navigate('/sell-vehicle')}>
                                                want to sell a used vehicle
                                            </button>

                                            <button type="button" className="btn btn-sm btn-outline-primary m-2" onClick={() => navigate('/buy-vehicle')}>
                                                want to buy a used vehicle
                                            </button>

                                            <button type="button" className="btn btn-sm btn-outline-secondary m-2" onClick={() => navigate('/others')}>
                                                other services
                                            </button>
                                        </div>
                                    </div>
                                </div>
                                <div className="card-body p-4 text-black">
                                    <div className="mb-5">
                                        <p className="lead fw-normal mb-1">About</p>
                                        <div className="p-4" style={{ backgroundColor: "#f8f9fa" }}>
                                            <div className="form-outline">
                                                <label className="form-label" htmlFor="name">Name</label>
                                                <input className="user-details form-control" value={userDetails.name} type="text" id="name" onChange={changeUserDetails} readOnly/>
                                            </div>

                                            <div className="form-outline">
                                                <label className="form-label" htmlFor="phone_no">Phone No</label>
                                                <input className="user-details form-control" value={userDetails.phone_no} type="text" id="phone_no" onChange={changeUserDetails} readOnly />
                                            </div>

                                            <div className="form-outline">
                                                <label className="form-label" htmlFor="email">Email</label>
                                                <input className="user-details form-control" value={userDetails.email} type="text" id="email" onChange={changeUserDetails} readOnly />
                                            </div>
                                            <div className="form-outline">
                                                <button type="button" className="btn btn-sm btn-outline-success update-btns"
                                                    onClick={saveUpdate}
                                                    style={{ marginTop: "15px" }}
                                                    hidden>
                                                    Save
                                                </button>
                                                <button type="button" className="btn btn-sm btn-outline-danger update-btns"
                                                    onClick={cancelUpdate}
                                                    style={{ marginTop: "15px", marginLeft: "15px" }}
                                                    hidden>
                                                    Cancel
                                                </button>
                                            </div>
                                            
                                        </div>
                                    </div>
                                </div>

                                <div className="card-body p-4 text-black">
                                    <p className="lead fw-normal mb-1">Upcoming Reminders</p>

                                    {reminders && reminders.length > 0 ?
                                        <table className="table table-striped projects">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: '' }}>
                                                        #
                                                    </th>
                                                    <th style={{ width: '' }}>
                                                        Owner Name
                                                    </th>
                                                    <th style={{ width: "" }} className="text-center">
                                                        Renewal Date
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        Action
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    reminders.map((reminder, index) => (
                                                        <Reminder key={reminder._id} reminder={reminder} index={index} />
                                                    ))
                                            
                                                }
                                        
                                            </tbody>
                                        </table>
                                        :
                                        <div className='bg bg-danger border border-secondary'>
                                            Haven't register yet
                                        </div>
                                    }
                                </div>

                                <div className="card-body p-4 text-black">
                                    <p className="lead fw-normal mb-1">Buy Requests</p>

                                    {buyrequests && buyrequests.length > 0 ?
                                        <table className="table table-striped projects">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: '' }}>
                                                        #
                                                    </th>
                                                    <th style={{ width: '' }}>
                                                        Type
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        Specification
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        Price Range
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        Action
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    buyrequests.map((buyrequest, index) => (
                                                        <BuyRequest key={buyrequest._id} buyrequest={buyrequest} index={index} />
                                                    ))

                                                }

                                            </tbody>
                                        </table>
                                        :
                                        <div className='bg bg-danger border border-secondary'>
                                            Haven't requested anything
                                        </div>
                                    }
                                </div>

                                <div className="card-body p-4 text-black">
                                    <p className="lead fw-normal mb-1">Sell Requests</p>

                                    {sellrequests && sellrequests.length > 0 ?
                                        <table className="table table-striped projects">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: '' }}>
                                                        #
                                                    </th>
                                                    <th style={{ width: '' }}>
                                                        Registration Number
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        vehicle specification
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        Price expected
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        status
                                                    </th>
                                                    <th style={{ width: "" }}>
                                                        Action
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    sellrequests.map((sellrequest, index) => (
                                                        <SellRequest key={sellrequest._id} sellrequest={sellrequest} index={index} />
                                                    ))

                                                }

                                            </tbody>
                                        </table>
                                        :
                                        <div className='bg bg-danger border border-secondary'>
                                            Haven't requested anything
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </div>
    );
}
