import React, { useState } from 'react';
import Navbar from '../components/Navbar';
import { Link, useNavigate } from 'react-router-dom';
import img from '../dist/img/img1.jpeg';
import Loader from '../components/Loader';
import Footer from '../components/Footer';

export default function Signup() {
    document.title = 'RNR | Signup';
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
    const [credentials, setCredentials] = useState({
        name: '',
        phone_no: '',
        password: ''
    });
    const [otp, setOtp] = useState('');

    const handleChange = (e) => {
        setCredentials({
            ...credentials,
            [e.target.id]: e.target.value
        });
    }

    const verifyOTP = async (e) => {
        e.preventDefault();
        setLoading(true);
        const response = await fetch('/api/verifyotp', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                phone_no: credentials.phone_no,
                otp
            })
        });
        setLoading(false);

        const json = await response.json();
        if (!json.success) {
            setTimeout(() => {
                alert(json.errors);
            }, 200);
        }
        else {
            document.getElementById('otp-form').setAttribute('hidden', true);
            document.getElementById('reg-form').removeAttribute('hidden');
            navigate('/login');
        }
    }
    const handleSubmit = async (e) => { 
        
        e.preventDefault();
        if (credentials.password !== document.getElementById('password_again').value) { 
            alert('Password and Confirm Password Should match');
            return;
        }

        setLoading(true);
        const response = await fetch('/api/createUser', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(credentials)
        });
        setLoading(false);

        const json = await response.json();
        if (!json.success) {
            setTimeout(() => {
                alert(json.errors);
            }, 200);
        }
        else {
            document.getElementById('reg-form').setAttribute('hidden', true);
            document.getElementById('otp-form').removeAttribute('hidden');
            alert("OTP sent to your mobile")
        }
    }


    return (
        <div>
            <Navbar />
            {loading && <Loader />}
            <section className="pb-3" style={{ backgroundColor: "#eee"}}>
                <div className="container h-100">
                    <div className="row d-flex justify-content-center h-100">
                        <div className="col-lg-12 col-xl-11 mt-3">
                            <div className="card text-black" style={{ borderRadius: "5px" }}>
                                <div className="card-body p-md-5">
                                    <div className="row justify-content-center">
                                        <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">

                                            <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">Sign up</p>

                                            <form className="mx-1 mx-md-4" onSubmit={handleSubmit} id="reg-form">

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fas fa-user fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input type="text" id="name" className="form-control border" value={ credentials.name } onChange={handleChange} required/>
                                                        <label className="form-label" htmlFor="name">Your Name</label>
                                                    </div>
                                                </div>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fa-solid fa-mobile-retro fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input type="text" id="phone_no" className="form-control border" value={credentials.phone_no} onChange={handleChange} required/>
                                                        <label className="form-label" htmlFor="phone_no">Your Phone Number</label>
                                                    </div>
                                                </div>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fas fa-lock fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input type="password" id="password" className="form-control border" value={credentials.password} onChange={handleChange} required/>
                                                        <label className="form-label" htmlFor="password">Password</label>
                                                    </div>
                                                </div>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fas fa-key fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input type="password" id="password_again" className="form-control border" required/>
                                                        <label className="form-label" htmlFor="password_again">Repeat your password</label>
                                                    </div>
                                                </div>
                                                {
                                                // <div className="form-check d-flex justify-content-center mb-5">
                                                //     <input className="form-check-input me-2" type="checkbox" value="" id="terms"
                                                //         onClick={
                                                //             e => document.querySelector('#registerBtn').disabled = !e.target.checked
                                                //         }
                                                //     />
                                                //     <label className="form-check-label" htmlFor="terms">
                                                //         I agree all statements in <a href="#!">Terms of service</a>
                                                //     </label>
                                                // </div>
                                                }
                                                

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button id="registerBtn" type="submit" className="btn btn-primary btn-lg">Register</button>
                                                </div>

                                                <div className="form-check d-flex justify-content-center mb-5">
                                                    <label className="form-check-label" htmlFor="terms">
                                                        already have an account <Link to="/login">signin</Link> now
                                                    </label>
                                                </div>

                                            </form>
                                            <form className="mx-1 mx-md-4" onSubmit={verifyOTP} id="otp-form" hidden>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fas fa-envelope fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input type="number" id="otp" className="form-control border" value={otp} onChange={(e)=>(setOtp(e.target.value))}/>
                                                        <label className="form-label" htmlFor="phone_no">Enter the 4 digit OTP</label>
                                                    </div>
                                                </div>

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button id="verifyBtn" type="submit" className="btn btn-primary btn-lg">Verify</button>
                                                </div>

                                            </form>

                                        </div>
                                        <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">

                                            <img src={img} className="img-fluid" alt="Sample image" />

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </div>
    );
}
