import { useEffect, useState } from "react";
import Navbar from "./components/Navbar";
import { useAdminContext } from "../hooks/useAdminContext";
import { useNavigate , useLocation } from "react-router-dom";

export default function VerifySellRequest(props) {
    const location = useLocation();
    const navigate = useNavigate();

    const _id = location.state?._id || null;
    
    const { state , dispatch} = useAdminContext();
    const { admin, sellrequests } = state;

    const [sellrequest, setSellRequest] = useState({});

    useEffect(() => {
        if (!admin)
            navigate('/admin');
    }, [state]);
    useEffect(() => {
        if (sellrequests)
            for (let x of sellrequests) {
                if (x._id === _id)
                    setSellRequest(x);
            }
    }, [_id, sellrequests]);

    const handleAccept = async() => {
        const response = await fetch('/api/updatesellrequest', {
            method: 'POST',
            body: JSON.stringify({
                _id: _id,
                status: 'verified'
            }),
            headers:{'Content-Type': 'application/json'}
        });
        const json = await response.json();
        if (json.success) {
            dispatch({
                type: 'UPDATE_SELL_REQUEST', payload: {
                    sellrequest: {
                        _id: _id,
                        status: 'verified'
                    } } });
            console.log(sellrequests);
            navigate('/admin/sellrequests');
        }
        else {
            alert('Failed to Update');
            navigate('/admin/sellrequests');
        }

    }

    const handleReject = async () => {
        if (!window.confirm('Are you sure you want to reject')) return;

        const response = await fetch(`/api/deletesellrequest/${_id}`, {
            method: 'DELETE'
        }
        );
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_SELL_REQUEST', payload: { sellrequest: json.sellrequest } });
        }
        navigate('/admin/sellrequests')
    }

    const handleBack = ()=> {
        navigate('/admin/sellrequests')
    }
    
    return (
        <div>
            <Navbar />
            

            <div className="row">
                <div className="col-lg-4">
                    <div id="carouselExampleIndicators" className="carousel slide" data-bs-ride="carousel">
                        <div className="carousel-indicators">
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="3" aria-label="Slide 4"></button>
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="4" aria-label="Slide 5"></button>
                        </div>
                        <div className="carousel-inner">
                            <div className="carousel-item active">
                                <img
                                    src={sellrequest?.owner_image?.url ?? "default_owner_image_url"}
                                    className="d-block w-100"
                                    alt="..."
                                />
                            </div>
                            <div className="carousel-item">
                                <img
                                    src={sellrequest?.adhar_image?.url ?? "default_adhar_image_url"}
                                    className="d-block w-100"
                                    alt="..."
                                />
                            </div>
                            <div className="carousel-item">
                                <img
                                    src={sellrequest?.rc_image?.url ?? "default_rc_image_url"}
                                    className="d-block w-100"
                                    alt="..."
                                />
                            </div>
                            <div className="carousel-item">
                                <img
                                    src={sellrequest?.side_image?.url ?? "default_side_image_url"}
                                    className="d-block w-100"
                                    alt="..."
                                />
                            </div>
                        </div>

                        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Previous</span>
                        </button>
                        <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Next</span>
                        </button>
                    </div>
                </div>
                <div className="col-lg-8">
                    <div className="card mb-4">
                        <div className="card-body">
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Registration Number</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.reg_no}</p>
                                </div>
                            </div>
                            <hr/>
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Phone Number</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.phone_no}</p>
                                </div>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Brand</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.brand}</p>
                                </div>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Specification</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.specification}</p>
                                </div>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Km Ran</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.km_ran}</p>
                                </div>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Age</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{`${sellrequest?.age} years`}</p>
                                </div>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Purchased</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.purchased}</p>
                                </div>
                            </div>
                            <hr />
                            <div className="row">
                                <div className="col-sm-3">
                                    <p className="mb-0">Expected Price</p>
                                </div>
                                <div className="col-sm-9">
                                    <p className="text-muted mb-0">{sellrequest?.price}</p>
                                </div>
                            </div>
                            <hr />
                            <div>
                                <button className="btn btn-outline-success btn-sm m-2"
                                    onClick={handleAccept}
                                >
                                    Accept
                                </button>
                                <button className="btn btn-outline-danger btn-sm m-2"
                                    onClick={handleReject}
                                >
                                    Reject
                                </button>
                                <button className="btn btn-outline-info btn-sm m-2"
                                    onClick={handleBack}
                                >
                                    Go Back
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </div>
    );
}