import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import { useNavigate } from "react-router-dom";
import { useUserContext } from "../hooks/useUserContext";
import Slider from 'rc-slider';
import Loader from "../components/Loader";
import 'rc-slider/assets/index.css';
import Footer from "../components/Footer";


export default function Others() {
    document.title = "RNR | Other Services";
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
    const { state, dispatch } = useUserContext();
    const { user } = state;
    const [info, setInfo] = useState({
        reg_no: '',
        phone_no: '',
        pincode: '',
        rc_expiry: '',
        fitness_expiry: '',
        dl_no: '',
        dl_expiry: '',
        lc_no: '',
        lc_expiry: ''
    });

   

    useEffect(() => {

        if (!user) {
            navigate('/');
        }
        
        
    }, [navigate, user]);

    const handleChange = (e) => {
        setInfo({
            ...info,
            [e.target.id]: e.target.value
        })
    };
    const toggle = (e, id) => {
        const wrapper = document.getElementById(id);
        if (e.target.checked) {
            wrapper.classList.remove('d-none');
            wrapper.classList.add('d-flex');
        }
        else {
            wrapper.classList.remove('d-flex');
            wrapper.classList.add('d-none');
        }
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (document.getElementById('rc_toggle').checked) {
            if (info.reg_no.trim() === '' || info.rc_expiry.trim() === '') {
                alert('Registration number and RC expiry date required');
                return;
            }
            const rcInfo = {
                reg_no: info.reg_no,
                phone_no: info.phone_no,
                pincode: info.pincode,
                rc_expiry: info.rc_expiry
            }
        }
        if (document.getElementById('fitness_toggle').checked) {
            if (info.reg_no.trim() === '' || info.fitness_expiry.trim() === '') {
                alert('Registration number and Fitness expiry date required');
                return;
            }
            const fitnessInfo = {
                reg_no: info.reg_no,
                phone_no: info.phone_no,
                pincode: info.pincode,
                fitness_expiry: info.fitness_expiry
            }
        }
        if (document.getElementById('dl_toggle').checked) {
            if (info.dl_no.trim() === '' || info.dl_expiry.trim() === '') {
                alert('DL number and DL expiry date required');
                return;
            }
            const dlInfo = {
                dl_no: info.dl_no,
                phone_no: info.phone_no,
                pincode: info.pincode,
                dl_expiry: info.dl_expiry
            }
        }
        if (document.getElementById('lc_toggle').checked) {
            if (info.lc_no.trim() === '' || info.lc_expiry.trim() === '') {
                alert('LC number and LC expiry date required');
                return;
            }
            const lcInfo = {
                lc_no: info.lc_no,
                phone_no: info.phone_no,
                pincode: info.pincode,
                lc_expiry: info.lc_expiry
            }
        }
        // setLoading(true);
        // const response = await fetch('api/buyrequest', {
        //     method: 'POST',
        //     body: JSON.stringify(vehicle),
        //     headers: { "Content-Type": "application/json" }
        // })
        // setLoading(false);
        // const json = await response.json();
        // if (json.success) {
        //     dispatch({ type: 'ADD_BUY_REQUEST', payload: { buyrequest: json.buyrequest } });
        //     navigate('/profile');
        // }
        // else
        //     setTimeout(() => {
        //         alert('Some Error occurred.\nPlease try again Later');
        //     }, 200);

    };


    return (
        <div>
            <Navbar />
            {loading && <Loader />}
            <section className="pb-3" style={{ backgroundColor: "#eee" }}>
                <div className="container">
                    <div className="row d-flex justify-content-center">
                        <div className="col-lg-12 col-xl-11 mt-3">
                            <div className="card text-black" style={{ borderRadius: "5px" }}>
                                <div className="card-body p-md-5">
                                    <div className="row justify-content-center">
                                        <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">
                                            <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">
                                                Other services
                                            </p>

                                            <form className="mx-1 mx-md-4" onSubmit={handleSubmit}>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="phone_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.phone_no}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Phone Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="pincode"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.pincode}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Pincode
                                                        </label>
                                                    </div>
                                                </div>

                                                <hr />
                                                

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="reg_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.reg_no}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Registration Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="dl_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.dl_no}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            DL Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="lc_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.lc_no}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            LC Number
                                                        </label>
                                                    </div>
                                                </div>


                                                <hr />


                                                <div className="form-check d-flex justify-content-start mb-5">
                                                    <input
                                                        className="form-check-input me-2"
                                                        type="checkbox"
                                                        id="rc_toggle"
                                                        onClick={(e)=>toggle(e,'rc_expiry_wrapper')}
                                                    />
                                                    <label className="form-check-label" htmlFor="rc_toggle">
                                                        Reminder for Registration Certificate expiry
                                                    </label>
                                                </div>
                                                <div className="d-none flex-row align-items-center mb-4" id="rc_expiry_wrapper">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="date"
                                                            id="rc_expiry"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.rc_expiry}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            RC expiry date
                                                        </label>
                                                    </div>
                                                </div>

                                                <hr />


                                                <div className="form-check d-flex justify-content-start mb-5">
                                                    <input
                                                        className="form-check-input me-2"
                                                        type="checkbox"
                                                        id="fitness_toggle"
                                                        onClick={(e) => toggle(e, 'fitness_expiry_wrapper')}
                                                    />
                                                    <label className="form-check-label" htmlFor="fitness_toggle">
                                                        Reminder for Fitness Certificate expiry
                                                    </label>
                                                </div>
                                                <div className="d-none flex-row align-items-center mb-4" id="fitness_expiry_wrapper">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="date"
                                                            id="fitness_expiry"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.fitness_expiry}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Fitness expiry date
                                                        </label>
                                                    </div>
                                                </div>

                                                <hr />


                                                <div className="form-check d-flex justify-content-start mb-5">
                                                    <input
                                                        className="form-check-input me-2"
                                                        type="checkbox"
                                                        id="dl_toggle"
                                                        onClick={(e) => {
                                                            toggle(e, 'dl_expiry_wrapper')
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="dl_toggle">
                                                        Reminder for Driving License Certificate expiry
                                                    </label>
                                                </div>
                                                <div className="d-none flex-row align-items-center mb-4" id="dl_expiry_wrapper">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="date"
                                                            id="dl_expiry"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.dl_expiry}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            DL expiry date
                                                        </label>
                                                    </div>
                                                </div>

                                                <hr />

                                                
                                                <div className="form-check d-flex justify-content-start mb-5">
                                                    <input
                                                        className="form-check-input me-2"
                                                        type="checkbox"
                                                        id="lc_toggle"
                                                        onClick={(e) => {
                                                            toggle(e, 'lc_expiry_wrapper')
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="lc_toggle">
                                                        Reminder for Labour Certificate expiry
                                                    </label>
                                                </div>
                                                <div className="d-none flex-row align-items-center mb-4" id="lc_expiry_wrapper">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="date"
                                                            id="lc_expiry"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={info.lc_expiry}
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            LC expiry date
                                                        </label>
                                                    </div>
                                                </div>
                                               

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button
                                                        id="submitBtn"
                                                        type="submit"
                                                        className="btn btn-primary btn-lg"
                                                    >
                                                        Submit
                                                    </button>
                                                </div>

                                            </form>
                                        </div>
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}
