import { useEffect, useState } from "react";
import Navbar from "./components/Navbar";
import { useAdminContext } from "../hooks/useAdminContext";
import { useNavigate } from "react-router-dom";

export default function BuyRequests() {
    const { state, dispatch } = useAdminContext();
    const { admin, users, buyrequests } = state;

    const navigate = useNavigate();
    useEffect(() => {
        if (!admin)
            navigate('/admin');
    }, [state]);

    const deleteBuyRequest = async (e) => {
        e.preventDefault();
        if (!window.confirm('Are you sure you want to delete')) return;

        const _id = e.target.parentElement.parentElement.getAttribute("id");
        const response = await fetch(`/api/buyrequest/${_id}`, {
            method: 'DELETE'
        });
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_BUY_REQUEST', payload: { buyrequest: json.buyrequest } });
        }
    }
    
    return (
        <div>
            <Navbar />
            <table className="table table-striped projects">
                <thead>
                    <tr>
                        <th style={{ width: '' }}>
                            #
                        </th>
                        <th style={{ width: '' }}>
                            Name
                        </th>
                        <th style={{ width: "" }} >
                            Phone Number
                        </th>
                        <th style={{ width: "" }} >
                            Brand
                        </th>
                        <th style={{ width: "" }}>
                            Specification
                        </th>
                        <th style={{ width: "" }}>
                            Price Range
                        </th>
                        <th style={{ width: "" }}>
                            Action
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {
                        buyrequests?.map((buyrequest, index) => (

                            <tr key={buyrequest._id} id={buyrequest._id}>
                                <td className="project-state">
                                    {index + 1}
                                </td>
                                <td className="project-state">
                                    {users.find((user)=>user._id == buyrequest.user).name}
                                </td>
                                <td className="project-state">
                                    {buyrequest.phone_no}
                                </td>
                                <td className="project-state">
                                    {buyrequest.brand}
                                </td>
                                <td className="project-state">
                                    {buyrequest.specification}
                                </td>
                                <td className="project-state">
                                    {buyrequest.price_range.min} - {buyrequest.price_range.max}
                                </td>
                                <td className="project-actions">
                                    <button
                                        className="btn btn-outline-danger btn-sm"
                                        onClick={deleteBuyRequest}
                                    >
                                        Delete
                                    </button>
                                </td>
                            </tr>

                        )
                        )
                    }
                </tbody>

            </table>
        </div>
    );
}