import React, { useEffect } from 'react';
import Navbar from './components/Navbar';
import { useAdminContext } from '../hooks/useAdminContext';
import { getAdminFromToken } from '../utils/Auth';
import { useNavigate } from 'react-router-dom';

export default function Dashboard() {
    const { state, dispatch } = useAdminContext();

    const token = localStorage.getItem('adminAuthToken'); 
    const navigate = useNavigate();

    async function fetchData() {
        let response = await fetch('/api/users');
        let json = await response.json();
        if (json.success) {
            dispatch({ type: "SET_USERS", payload: { users: json.users } });
        }
        else {
            dispatch({ type: "SET_USERS", payload: { users: null } });
        }

        response = await fetch('/api/reminders');
        json = await response.json();
        if (json.success) {
            dispatch({ type: "SET_REMINDERS", payload: { reminders: json.reminders } });
        }
        else {
            dispatch({ type: "SET_REMINDERS", payload: { reminders: null } });
        }

        response = await fetch('/api/sellrequests');
        json = await response.json();
        if (json.success) {
            dispatch({ type: "SET_SELL_REQUESTS", payload: { sellrequests: json.sellrequests } });
        }
        else {
            dispatch({ type: "SET_SELL_REQUESTS", payload: { sellrequests: null } });
        }
        response = await fetch('/api/buyrequest');
        json = await response.json();
        if (json.success) {
            dispatch({ type: "SET_BUY_REQUESTS", payload: { buyrequests: json.buyrequests } });
        }
        else {
            dispatch({ type: "SET_BUY_REQUESTS", payload: { buyrequests: null } });
        }
        response = await fetch('/api/message');
        json = await response.json();
        if (json.success) {
            dispatch({ type: "SET_MESSAGES", payload: { messages: json.messages } });
        }
        else {
            dispatch({ type: "SET_MESSAGES", payload: { messages: null } });
        }
    }

    useEffect(() => {
        

        if (!token) {
            dispatch({ type: 'SET_ADMIN', payload: { admin: null } });
            navigate('/admin/login');
        }
        const adminData = getAdminFromToken(token);
        if (!adminData) {
            dispatch({ type: 'SET_ADMIN', payload: { admin: null } });
            navigate('/admin/login');
        }
        else {
            dispatch({ type: 'SET_ADMIN', payload: { admin: adminData } });
            fetchData();
        }
    }, [dispatch]);
    return (
            <Navbar />
    );
}