import React, { useState } from 'react';
import Navbar from '../components/Navbar';
import { Link, useNavigate } from 'react-router-dom';
import img from '../dist/img/img1.jpeg';
import Loader from '../components/Loader';
import Footer from '../components/Footer';


export default function Login() {
    const [loading, setLoading] = useState(false);

    document.title = 'RNR | Signin';
    const navigate = useNavigate();
    const [credentials, setCredentials] = useState({
        phone_no: '',
        password: ''
    });

    const handleChange = (e) => {
        setCredentials({
            ...credentials,
            [e.target.id]: e.target.value
        });
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        const response = await fetch('/api/loginuser', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(credentials)
        });
        const json = await response.json();
        
        setLoading(false);
        if (!json.success) {
            setTimeout(() => {
                alert('Invalid Credentials');
            }, 200);
        }
        else {
            localStorage.setItem('authToken', json.authToken);
            navigate('/profile');
        }
    }

    return (
        <div>
            {loading && <Loader />}
            <Navbar />
            <section className="pb-3" style={{ backgroundColor: "#eee" }}>
                <div className="container h-100">
                    <div className="row d-flex justify-content-center h-100">
                        <div className="col-lg-12 col-xl-11 mt-3">
                            <div className="card text-black" style={{ borderRadius: "5px" }}>
                                <div className="card-body p-md-5">
                                    <div className="row justify-content-center">
                                        <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">

                                            <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">Login</p>

                                            <form className="mx-1 mx-md-4" onSubmit={handleSubmit}>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fa-solid fa-mobile-retro fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input type="text" id="phone_no" className="form-control border" value={credentials.phone_no} onChange={handleChange} />
                                                        <label className="form-label" htmlFor="phone_no">Your Phone Number</label>
                                                    </div>
                                                </div>

                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <i className="fas fa-lock fa-lg me-3 fa-fw"></i>
                                                    <div className="form-outline flex-fill mb-0">
                                                        <div className='d-flex text-center'>
                                                            <input type="password" id="password" className="form-control border" value={credentials.password} onChange={handleChange} />
                                                            <i className="bi bi-eye-slash"
                                                                style={{
                                                                    marginLeft: "-25px",
                                                                    cursor: "pointer",
                                                                    marginTop: "auto",
                                                                    marginBottom: "auto"
                                                                }}
                                                                onClick={
                                                                    (e) => {
                                                                        const password = document.getElementById('password');
                                                                        const type = password.getAttribute("type") === "password" ? "text" : "password";
                                                                        password.setAttribute("type", type);

                                                                        // toggle the icon
                                                                        e.target.classList.toggle("bi-eye");
                                                                    }
                                                                }></i>
                                                        </div>
                                                        <label className="form-label" htmlFor="password">Password</label>
                                                    </div>
                                                </div>

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button id="loginBtn" type="submit" className="btn btn-primary btn-lg">Login</button>
                                                </div>


                                                <div className="form-check d-flex justify-content-center mb-5">
                                                    <label className="form-check-label" htmlFor="terms">
                                                        don't have an account <Link to="/signup">signup</Link> now
                                                    </label>
                                                </div>

                                            </form>

                                        </div>
                                        <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">

                                            <img src={img}
                                                className="img-fluid" alt="Sample image" />

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </div>
    );
}
