import { useEffect, useState } from "react";
import Navbar from "./components/Navbar";
import { useAdminContext } from "../hooks/useAdminContext";
import { useNavigate } from "react-router-dom";

export default function TodaysReminder() {
    const { state } = useAdminContext();
    const { admin, reminders } = state;
    const navigate = useNavigate();
    useEffect(() => {
        if (!admin)
            navigate('/admin');
    }, [state]);
    return (
        <div>
            <Navbar />
            <table className="table table-striped projects">
                <thead>
                    <tr>
                        <th style={{ width: '' }}>
                            #
                        </th>
                        <th style={{ width: '' }}>
                            Owner Name
                        </th>
                        <th style={{ width: "" }} >
                            Phone Number
                        </th>
                        <th style={{ width: "" }} >
                            Renewal Date
                        </th>
                        <th style={{ width: "" }} >
                            Status
                        </th>
                        <th style={{ width: "" }}>
                            Action
                        </th>
                    </tr>
                </thead>

                {
                    reminders?.filter((r) =>
                        new Date(r.expiry_date).toLocaleDateString() ===
                        new Date().toLocaleDateString()
                    ).map((reminder, index) => (
                        <tbody key={reminder._id}>

                            <tr>
                                <td className="project-state">
                                    {index + 1}
                                </td>
                                <td className="project-state">
                                    {reminder.owner_name}
                                </td>
                                <td className="project-state">
                                    {reminder.phone_no}
                                </td>
                                <td className="project-state">
                                    {new Date(reminder.expiry_date).toDateString()}
                                </td>
                                <td className="project-state">
                                    {reminder.status}
                                </td>
                                <td className="project-actions text-right">

                                    <button className="btn btn-secondary btn-sm">
                                        <i className="fas fa-trash">
                                        </i>
                                        Delete
                                    </button>
                                </td>
                            </tr>

                        </tbody>
                    )
                    )
                }

            </table>
        </div>
    );
}