import { useEffect, useState } from "react";
import Navbar from "./components/Navbar";
import { useAdminContext } from "../hooks/useAdminContext";
import { useNavigate } from "react-router-dom";

export default function Messages() {
    const { state, dispatch } = useAdminContext();
    const { admin, messages } = state;

    const navigate = useNavigate();
    useEffect(() => {
        if (!admin)
            navigate('/admin');
    }, [state]);

    const deleteMessage = async (e) => {
        e.preventDefault();
        if (!window.confirm('Are you sure you want to delete')) return;

        const _id = e.target.parentElement.parentElement.getAttribute("id");
        const response = await fetch(`/api/message/${_id}`, {
            method: 'DELETE'
        });
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_MESSAGE', payload: { message: json.message } });
        }
    }

    return (
        <div>
            <Navbar />
            <table className="table table-striped projects">
                <thead>
                    <tr>
                        <th style={{ width: '' }}>
                            #
                        </th>
                        <th style={{ width: '' }}>
                            Name
                        </th>
                        <th style={{ width: "" }} >
                            Phone Number
                        </th>
                        <th style={{ width: "" }} >
                            Date
                        </th>
                        <th style={{ width: "" }}>
                            Message
                        </th>
                        <th style={{ width: "" }}>
                            Action
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {
                        messages?.map((message, index) => (

                            <tr key={message._id} id={message._id}>
                                <td className="project-state">
                                    {index + 1}
                                </td>
                                <td className="project-state">
                                    {message.name}
                                </td>
                                <td className="project-state">
                                    {message.phone_no}
                                </td>
                                <td className="project-state">
                                    {new Date(message.date).toDateString()}
                                </td>
                                <td className="project-state">
                                    {message.message}
                                </td>
                                <td className="project-actions">
                                    <button
                                        className="btn btn-outline-danger btn-sm"
                                        onClick={deleteMessage}
                                    >
                                        Delete
                                    </button>
                                </td>
                            </tr>

                        )
                        )
                    }
                </tbody>

            </table>
        </div>
    );
}