import { useEffect, useState } from "react";
import Navbar from "./components/Navbar";
import { useAdminContext } from "../hooks/useAdminContext";
import { useNavigate } from "react-router-dom";

export default function Users() {
    const { state, dispatch } = useAdminContext();
    const { admin, users } = state;
    
    const navigate = useNavigate();
    useEffect(() => {
        if (!admin)
            navigate('/admin');
    }, [state]);
    const handleDelete = async (_id) => {
        if (!window.confirm('Are you sure you want to delete')) return;
        const response = await fetch(`/api/deleteuser/${_id}`, {
            method: 'DELETE'
        }
        );
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_USER', payload: { user: json.user } });
            dispatch({ type: 'SET_REMINDERS', payload: { reminders: json.reminders } });
            dispatch({ type: 'SET_SELL_REQUESTS', payload: { sellrequests: json.sellrequests } });
        }
    }
    return (
        <div>
            <Navbar/>
            <table className="table table-striped projects">
                <thead>
                    <tr>
                        <th style={{ width: '' }}>
                            #
                        </th>
                        <th style={{ width: '' }}>
                            User Name
                        </th>
                        <th style={{ width: "" }} >
                            Phone Number
                        </th>
                        <th style={{ width: "" }}>
                            Action
                        </th>
                    </tr>
                </thead>
                {
                    users?.map((user, index) => (
                        <tbody key={user._id}>

                            <tr>
                                <td className="project-state">
                                    {index+1}
                                </td>
                                <td className="project-state">
                                    {user.name}
                                </td>
                                <td className="project-state">
                                    {user.phone_no}
                                </td>
                                <td className="project-actions text-right">

                                    <button className="btn btn-secondary btn-sm"
                                    onClick={()=>handleDelete(user._id)}>
                                        <i className="fas fa-trash">
                                        </i>
                                        Delete
                                    </button>
                                </td>
                            </tr>

                        </tbody>
                        )
                    )
                }
                
            </table>
        </div>
    );
}