import { useEffect, useState } from "react";
import Navbar from "./components/Navbar";
import { useAdminContext } from "../hooks/useAdminContext";
import { useNavigate } from "react-router-dom";

export default function SellRequests() {
    const { state } = useAdminContext();
    const { admin, sellrequests,users } = state;

    const navigate = useNavigate();
    useEffect(() => {
        if (!admin)
            navigate('/admin');
    }, [state]);
    const verifySellRequest = (e) => {
        const _id = e.target.parentElement.parentElement.getAttribute("id");
        navigate('/admin/verify-sell-request', { state: { _id } });
    }
    return (
        <div>
            <Navbar />
            <table className="table table-striped projects">
                <thead>
                    <tr>
                        <th style={{ width: '' }}>
                            #
                        </th>
                        <th style={{ width: '' }}>
                            Owner Name
                        </th>
                        <th style={{ width: '' }}>
                            Image
                        </th>
                        <th style={{ width: '' }}>
                            Registration No
                        </th>
                        <th style={{ width: "" }} >
                            Phone Number
                        </th>
                        <th style={{ width: "" }} >
                            Status
                        </th>
                        <th style={{ width: "" }}>
                            Action
                        </th>
                    </tr>
                </thead>
                <tbody>
                {
                    sellrequests?.map((sellrequest, index) => (

                        <tr key={sellrequest._id} id={sellrequest._id}>
                                <td className="project-state">
                                    {index + 1}
                                </td>
                                <td className="project-state">
                                {users.find((user) => user._id == sellrequest.user).name}
                                </td>
                                <td className="project-state">
                                    <img src={sellrequest.owner_image.url}
                                        alt="Owner"
                                        style={{width: '50px', aspectRatio:1}}
                                    />
                                </td>
                                <td className="project-state">
                                    {sellrequest.reg_no}
                                </td>
                                <td className="project-state">
                                    {sellrequest.phone_no}
                                </td>

                                <td className="project-state">
                                {
                                    sellrequest.status === 'verified'
                                        ?
                                        <span className="badge bg-success">Verified</span>
                                        :
                                        <span className="badge bg-danger">Not Verified</span>

                                }
                                </td>
                                <td className="project-actions">
                                    <button
                                        className="btn btn-outline-info btn-sm"
                                        onClick={verifySellRequest}
                                    >
                                        View
                                    </button>
                                </td>
                            </tr>

                    )
                    )
                }
                </tbody>

            </table>
        </div>
    );
}