import React from 'react';
import { useUserContext } from '../hooks/useUserContext';


export default function SellRequest({ sellrequest, index }) {
    const { dispatch } = useUserContext();
    const handleDelete = async () => {
        if (!window.confirm('Are you sure you want to delete')) return;

        const response = await fetch(`/api/deletesellrequest/${sellrequest._id}`, {
            method: 'DELETE'
        }
        );
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_SELL_REQUEST', payload: { sellrequest: json.sellrequest } });
        }
    }
    return (
        <tr>
            <td>
                {index + 1}
            </td>
            <td>
                {sellrequest.reg_no}
            </td>
            <td>
                {`${sellrequest.brand} ${sellrequest.specification}`}
            </td>
            <td>
                {sellrequest.price}
            </td>
            <td>
                {sellrequest.status}
            </td>
            <td className="project-actions text-right">

                <button className="btn btn-secondary btn-sm" onClick={handleDelete}>
                    <i className="fas fa-trash">
                    </i>
                </button>
            </td>
        </tr>

    );
}