import React, { useEffect, useState} from "react";
import Navbar from "../components/Navbar";
import {useNavigate } from "react-router-dom";
import { useUserContext } from "../hooks/useUserContext";
import AddressForm from "../components/AddressForm";
import Loader from "../components/Loader";
import Footer from "../components/Footer";


export default function Sell() {
    document.title = "RNR | Sell";
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
    const { state, dispatch } = useUserContext();
    const { user } = state;

    const [vehicle, setVehicle] = useState({
        user: null,
        reg_no: null,
        phone_no: null,
        price: null,
        brand: null,
        type: null,
        specification: null,
        km_ran: null,
        age: null,
        purchased: 'new',
        address: null,
        owner_image: null,
        adhar_image: null,
        rc_image: null,
        side_image: null
    });

    useEffect(() => {
        if (!user) {
            navigate('/profile');
        }
        setVehicle({
            ...vehicle,
            user: user?._id
        })
    }, [navigate, user]);

    const handleChange = (e) => {
        setVehicle({
            ...vehicle,
            [e.target.id]:e.target.value
        });
    };
    const setImageFields = (e) => {
        const file = e.target.files[0];
        if (file) {
            if (file.size > 2000000) {
                alert('Image size should be less than 2 MB.');
                return;
            }
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onloadend = () => {
                setVehicle({
                    ...vehicle,
                    [e.target.id]: reader.result,
                });
            };
        }
    }
    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!vehicle.address) 
            return alert("Please enter address");
        
        
        setLoading(true);
        const response = await fetch('api/upload', {
            method: 'POST',
            body: JSON.stringify(vehicle),
            headers:{"Content-Type": "application/json"}
        })
        setLoading(false);
        const json = await response.json();
        if (json.success) {
            dispatch({ type: 'ADD_SELL_REQUEST', payload: { sellrequest: json.sellrequest } });

            navigate('/profile');
        }
        else
            setTimeout(() => {
                alert('Some Error occurred.\nPlease try again Later');
            }, 200);
        
    };


    return (
        <div>
            <Navbar />
            {loading && <Loader/>}
            <section className="pb-3" style={{ backgroundColor: "#eee" }}>
                <div className="container">
                    <div className="row d-flex justify-content-center">
                        <div className="col-lg-12 col-xl-11 mt-3">
                            <div className="card text-black" style={{ borderRadius: "5px" }}>
                                <div className="card-body p-md-5">
                                    <div className="row justify-content-center">
                                        <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">
                                            <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">
                                                Sell a used vehicle
                                            </p>

                                            <form className="mx-1 mx-md-4" onSubmit={handleSubmit}>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="reg_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.reg_no?.toUpperCase()}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Registration Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="phone_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.phone_no}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Phone Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="number"
                                                            id="price"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.price}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Price Expected
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="brand"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.brand}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Brand
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="type"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.type}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Type Of Vehicle
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="specification"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.specification}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Vehicle Specification
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="number"
                                                            id="km_ran"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.km_ran}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Km Ran
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="number"
                                                            id="age"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.age}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Age of the vehicle (in years)
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <select
                                                            type="text"
                                                            id="purchased"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.purchased}
                                                            required
                                                        >
                                                            <option value='new'>New</option>
                                                            <option value='old'>Old</option>
                                                        </select>
                                                        <label className="form-label" htmlFor="name">
                                                            Purchased New / Old
                                                        </label>
                                                    </div>
                                                </div>

                                                {/* Address Field */}
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <AddressForm
                                                            className="form-control border"
                                                            onChange={(address) => setVehicle({
                                                                ...vehicle,
                                                                address: address
                                                            })}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Address
                                                        </label>
                                                    </div>
                                                </div>
                                                {/* Image Field */}
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="file"
                                                            id="owner_image"
                                                            accept="image/*"
                                                            className="form-control border"
                                                            onChange={setImageFields}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Owner's Photo With The Vehical Showing Numberplate
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="file"
                                                            id="adhar_image"
                                                            accept="image/*"
                                                            className="form-control border"
                                                            onChange={setImageFields}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Image of your Adhar Card
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="file"
                                                            id="rc_image"
                                                            accept="image/*"
                                                            className="form-control border"
                                                            onChange={setImageFields}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Image of your Vehicle Registration Card
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="file"
                                                            id="side_image"
                                                            accept="image/*"
                                                            className="form-control border"
                                                            onChange={setImageFields}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Image of your vehicle from left / right side
                                                        </label>
                                                    </div>
                                                </div>

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button
                                                        id="registerBtn"
                                                        type="submit"
                                                        className="btn btn-primary btn-lg"
                                                    >
                                                        Submit
                                                    </button>
                                                </div>

                                            </form>
                                        </div>
                                        <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2 h-100">
                                            <img
                                                src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp"
                                                className="img-fluid"
                                                alt="Sample image"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </div>
    );
}
