import React from 'react';
import { useUserContext } from '../hooks/useUserContext';


export default function Reminder({ reminder, index }) {
    const { dispatch } = useUserContext();
    const handleDelete = async () => {
        if (!window.confirm('Are you sure you want to delete')) return;

        const response = await fetch(`/api/deletereminders/${reminder._id}`, {
            method: 'DELETE'
        }
        );
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_REMINDER', payload: { reminder: json.reminder } });
        }
    }
    return (
        <tr>
            <td>
                {index+1}
            </td>
            <td>
                {reminder.owner_name}
            </td>
            <td>
                {new Date(reminder.expiry_date).toDateString()}
            </td>
           
            <td className="project-actions text-right">
                
                <button className="btn btn-secondary btn-sm" onClick={handleDelete}>
                    <i className="fas fa-trash">
                    </i>
                </button>
            </td>
        </tr>
                
    );
}