import { createContext, useReducer } from "react";

export const AdminContext = createContext();

export const AdminReducer = (state, action) => {
    switch (action.type) {
        case "SET_NULL":
            return {
                admin: null,
                users: null,
                reminders: null
            }
        case "SET_ADMIN":
            return {
                ...state,
               admin: action.payload.admin 
            }
        case "SET_USERS":
            return {
                ...state,
                users: action.payload.users
            }
        case 'REMOVE_USER':
            return {
                ...state,
                users: state.users.filter(item => item._id !== action.payload.user._id)
            }
        case "SET_REMINDERS":
            return {
                ...state,
                reminders: action.payload.reminders
            }
        case "SET_SELL_REQUESTS":
            return {
                ...state,
                sellrequests: action.payload.sellrequests
            }
        case "UPDATE_SELL_REQUEST":
            const index = state.sellrequests.findIndex(item => item._id === action.payload.sellrequest._id);
            const updatedObject = { ...state.sellrequests[index], ...action.payload.sellrequest };
            const updatedArray = [
                ...state.sellrequests.slice(0, index),
                updatedObject,
                ...state.sellrequests.slice(index + 1)
            ];
            return {
                ...state,
                sellrequests: updatedArray
            }
        
        case 'REMOVE_SELL_REQUEST':
            return {
                ...state,
                sellrequests: state.sellrequests.filter(item => item._id !== action.payload.sellrequest._id)
            }
        case "SET_BUY_REQUESTS":
            return {
                ...state,
                buyrequests: action.payload.buyrequests
            }
        case 'REMOVE_BUY_REQUEST':
            return {
                ...state,
                buyrequests: state.buyrequests.filter(item => item._id !== action.payload.buyrequest._id)
            }
        case "SET_MESSAGES":
            return {
                ...state,
                messages: action.payload.messages
            }
        case 'REMOVE_MESSAGE':
            return {
                ...state,
                messages: state.messages.filter(message => message._id !== action.payload.message._id)
            }   
        default:
            return state;
    }
}

export const AdminContextProvider = ({ children }) => {

    const [state, dispatch] = useReducer(AdminReducer, {
        admin: null,
        users: null,
        reminders: null,
        sellrequests: null,
        buyrequests: null,
        messages: null
    });


    return (
        <AdminContext.Provider value={{ state, dispatch }}>
            {children}
        </AdminContext.Provider>
    );
}