import React from 'react';
import { useUserContext } from '../hooks/useUserContext';


export default function BuyRequest({ buyrequest, index }) {
    const { dispatch } = useUserContext();
    const handleDelete = async () => {
        if (!window.confirm('Are you sure you want to delete')) return;

        const response = await fetch(`/api/buyrequest/${buyrequest._id}`, {
            method: 'DELETE'
        }
        );
        const json = await response.json();
        if (!json.success) {
            alert(json.message);
        }
        else {
            dispatch({ type: 'REMOVE_BUY_REQUEST', payload: { buyrequest: json.buyrequest } });
        }
    }
    return (
        <tr>
            <td>
                {index + 1}
            </td>
            <td>
                {buyrequest.type}
            </td>
            <td>
                {buyrequest.specification}
            </td>
            <td>
                {buyrequest.price_range.min} - {buyrequest.price_range.max}
            </td>

            <td className="project-actions text-right">

                <button className="btn btn-secondary btn-sm" onClick={handleDelete}>
                    <i className="fas fa-trash">
                    </i>
                </button>
            </td>
        </tr>

    );
}