import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import  '../dist/css/Home.css'
import { useNavigate } from 'react-router-dom';
import { useUserContext } from '../hooks/useUserContext';
import { getUserFromToken } from '../utils/Auth';
import Footer from '../components/Footer';

export default function Home() {

    document.title = 'RNR | Home';

    const navigate = useNavigate();
    

    const { state, dispatch } = useUserContext();

    // Simulate getting the JWT token from a stored location (localStorage or cookies)
    const token = localStorage.getItem('authToken'); // Replace with the actual JWT token


    const fetchReminders = async (userId) => {
        try {
            // Fetch reminders for the user
            const response = await fetch(`/api/reminders?user=${userId}`);
            const json = await response.json();
            if (json.success) {
                // set reminders
                dispatch({ type: 'SET_REMINDERS', payload: { reminders:json.reminders } });
            } else {
                // Handle the case when there are no reminders or some error occurred
            }
        } catch (error) {
            console.error('Error fetching reminders:', error);
            // Handle the error, e.g., show an error message to the user
        }

    };
    const fetchBuyRequests = async (userId) => {
        try {
            const response = await fetch(`/api/buyrequest/${userId}`);
            const json = await response.json();
            if (json.success) {
                dispatch({ type: 'SET_BUY_REQUESTS', payload: { buyrequests: json.buyrequests } });
            }
        } catch (error) {
            console.error('Error fetching buyrequests:', error);
        }

    };
    const fetchSellRequests = async (userId) => {
        try {
            const response = await fetch(`/api/sellrequests/?user=${userId}`);
            const json = await response.json();
            if (json.success) {
                dispatch({ type: 'SET_SELL_REQUESTS', payload: { sellrequests: json.sellrequests } });
            }
        } catch (error) {
            console.error('Error fetching sellrequests:', error);
        }

    };

    const fetchUser = async (token) => { 
        const user = await getUserFromToken(token);
        return user;
    }

    useEffect(() => {
        const fetchUserData = async () => {
            if (!token) {
                dispatch({ type: 'SET_USER', payload: { user: null } });
            } else {
                try {
                    const userData = await fetchUser(token);
                    dispatch({ type: 'SET_USER', payload: { user: userData } });
                    await fetchReminders(userData._id);
                    await fetchBuyRequests(userData._id);
                    await fetchSellRequests(userData._id);
                } catch (error) {
                    // Handle any errors that might occur during fetchUser
                    dispatch({ type: 'SET_USER', payload: { user: null } });
                }
            }
        };

        fetchUserData();
    }, [token]);



    return (
        <div>
            <Navbar />

            <section className="home-section d-flex align-items-center justify-content-center">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12 text-center">
                            <h1 className="display-4 mb-4">Rely on us and Relax</h1>
                            
                            <button className="btn btn-primary btn-lg" onClick={()=>navigate('/profile')}>
                                Get Started
                            </button>
                        </div>
                    </div>
                </div>
            </section>

            <Footer/>

            

        </div>
    )
}
