import { createContext, useReducer } from "react";

export const UserContext = createContext();

export const UserReducer = (state, action) => {
    switch (action.type) { 
        case "SET_USER":
            return {
                ...state,
                user: action.payload.user
            }
        // reminders
        case "SET_REMINDERS":
            return {
                ...state,
                reminders: action.payload.reminders
            }
        case "ADD_REMINDER":
            return {
                ...state,
                reminders: [action.payload.reminder, ...state.reminders]
            }
        case "REMOVE_REMINDER":
            return {
                ...state,
                reminders: state.reminders.filter((reminder) => reminder._id !== action.payload.reminder._id)
            }
        // sellrequests
        case "SET_SELL_REQUESTS":
            return {
                ...state,
                sellrequests: action.payload.sellrequests
            }
        case "ADD_SELL_REQUEST":
            console.log(state)
            return {
                ...state,
                sellrequests: [action.payload.sellrequest, ...state.sellrequests]
            }
        case "REMOVE_SELL_REQUEST":
            return {
                ...state,
                sellrequests: state.sellrequests.filter((sellrequest) => sellrequest._id !== action.payload.sellrequest._id)
            }
        // buyrequests
        case "SET_BUY_REQUESTS":
            return {
                ...state,
                buyrequests: action.payload.buyrequests
            }
        case "ADD_BUY_REQUEST":
            return {
                ...state,
                buyrequests: [action.payload.buyrequest, ...state.buyrequests]
            }
        case "REMOVE_BUY_REQUEST":
            return {
                ...state,
                buyrequests: state.buyrequests.filter((buyrequest) => buyrequest._id !== action.payload.buyrequest._id)
            }
        default:
            return state
    }
}

export const UserContextProvider = ({ children }) => {
    
    const [state, dispatch] = useReducer(UserReducer, {
        user: null,
        reminders: null,
        sellrequests: null,
        buyrequests: null
    });


    return (
        <UserContext.Provider value={{ state, dispatch }}>
            {children}
        </UserContext.Provider>
    );
}