import React, { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import { useNavigate } from "react-router-dom";
import { useUserContext } from "../hooks/useUserContext";
import Slider from 'rc-slider';
import Loader from "../components/Loader";
import 'rc-slider/assets/index.css';
import Footer from "../components/Footer";


export default function Buy() {
    document.title = "RNR | Buy";
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
    const { state, dispatch } = useUserContext();
    const { user } = state;

    const [vehicle, setVehicle] = useState({
        user: null,
        phone_no: null,
        brand: null,
        type: null,
        specification: null,
        price_range: {
            min: 5000,
            max: 10000
        
        }
    });

    useEffect(() => {

        if (!user) {
            navigate('/');
        }
        setVehicle({
            ...vehicle,
            user: user._id
        })
    }, [navigate, user]);

    const handleChange = (e) => {
        setVehicle({
            ...vehicle,
            [e.target.id]: e.target.value
        });
    };
    
    const handleSubmit = async (e) => {
        e.preventDefault();
        
        setLoading(true);
        const response = await fetch('api/buyrequest', {
            method: 'POST',
            body: JSON.stringify(vehicle),
            headers: { "Content-Type": "application/json" }
        })
        setLoading(false);
        const json = await response.json();
        if (json.success) {
            dispatch({ type: 'ADD_BUY_REQUEST', payload: { buyrequest: json.buyrequest } });
            navigate('/profile');
        }
        else
            setTimeout(() => {
                alert('Some Error occurred.\nPlease try again Later');
            }, 200);

    };


    return (
        <div>
            <Navbar />
            {loading && <Loader />}
            <section className="pb-3" style={{ backgroundColor: "#eee" }}>
                <div className="container">
                    <div className="row d-flex justify-content-center">
                        <div className="col-lg-12 col-xl-11 mt-3">
                            <div className="card text-black" style={{ borderRadius: "5px" }}>
                                <div className="card-body p-md-5">
                                    <div className="row justify-content-center">
                                        <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">
                                            <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">
                                                Buy a used vehicle
                                            </p>

                                            <form className="mx-1 mx-md-4" onSubmit={handleSubmit}>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="phone_no"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.phone_no}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Phone Number
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="brand"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.brand}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Brand
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="type"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.type}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Type Of Vehicle
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <input
                                                            type="text"
                                                            id="specification"
                                                            className="form-control border"
                                                            onChange={handleChange}
                                                            value={vehicle.specification}
                                                            required
                                                        />
                                                        <label className="form-label" htmlFor="name">
                                                            Vehicle Specification
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="d-flex flex-row align-items-center mb-4">
                                                    <div className="form-outline flex-fill mb-0">
                                                        <Slider
                                                            range
                                                            min={5000}
                                                            max={500000}
                                                            step={1000}
                                                            value={[vehicle.price_range.min, vehicle.price_range.max]}
                                                            onChange={
                                                                (e) => {
                                                                    setVehicle({
                                                                        ...vehicle,
                                                                        price_range: {
                                                                            min: e[0],
                                                                            max: e[1]
                                                                        }
                                                                    })
                                                                }
                                                            }
                                                        />
                                                        <div>
                                                            Min Price: {vehicle.price_range.min} | Max Price: {vehicle.price_range.max}
                                                        </div>
                                                        <label className="form-label" htmlFor="name">
                                                            Price range
                                                        </label>
                                                    </div>
                                                </div>

                                                <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button
                                                        id="submitBtn"
                                                        type="submit"
                                                        className="btn btn-primary btn-lg"
                                                    >
                                                        Submit
                                                    </button>
                                                </div>

                                            </form>
                                        </div>
                                        <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2 h-100">
                                            <img
                                                src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp"
                                                className="img-fluid"
                                                alt="Sample image"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </div>
    );
}
