import { Link, useNavigate } from "react-router-dom";
import { useAdminContext } from "../../hooks/useAdminContext";

export default function Navbar() {
    const { state , dispatch} = useAdminContext();
    const { admin } = state;
    const navigate = useNavigate();
    const handleLogout = () => {
        localStorage.removeItem('adminAuthToken');
        dispatch({ type: 'SET_NULL' });
        navigate('/admin');
    }
    return (
        <nav className="navbar navbar-expand-lg navbar-light">
            <div className="container-fluid">
                <Link className="navbar-brand fs-1" to="/">RelyNRelax</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav me-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin/users">Users</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin/registrations">Registrations</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin/todaysreminders">Today's Reminders</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin/sellrequests">Sell Requests</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin/buyrequests">Buy Requests</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/admin/messages">Messages</Link>
                        </li>
                    </ul>

                    {admin
                        ?
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <div className="btn btn-secondary" onClick={handleLogout}>Logout</div>
                            </li>
                        </ul>
                        : ''
                    }
                </div>
            </div>
        </nav>
    );
}